body {
  font-size: 14px;
  line-height: 1.42857143; }

a {
  color: #bb8462;
  -o-transition: background-image 2s ease-in-out;
  -webkit-transition: background-image 2s ease-in-out;
  transition: background-image 2s ease-in-out;
  text-decoration: none; }

a:hover {
  background-image: -webkit-linear-gradient(top, #d5b096 25%, #97461a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -o-transition: background-image 2s ease-in-out;
  -webkit-transition: background-image 2s ease-in-out;
  transition: background-image 2s ease-in-out; }

h2 {
  margin-top: 10px;
  margin-bottom: 20px;
  font-family: "Mitr", sans-serif;
  font-size: 18px;
  overflow: hidden;
  text-align: center; }
  h2:after {
    content: "";
    display: block;
    position: relative;
    text-align: center;
    vertical-align: bottom;
    width: 10%;
    margin: 16px auto 4px;
    border-top: 1px solid #8c8c8c; }

.navbar-default {
  background-color: #fff;
  text-align: center; }

.navbar {
  border: 0px;
  margin: 0px; }

#nav {
  min-height: 62px;
  margin: 0 auto;
  font-family: "Mitr", sans-serif; }
  #nav li {
    float: left;
    list-style-type: none; }
  #nav a {
    display: block;
    text-align: center;
    width: 150px;
    /* fixed width */
    text-decoration: none;
    color: #0a402b; }
  #nav a:hover {
    background-image: -webkit-linear-gradient(top, #d5b096 25%, #97461a);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -o-transition: background-image 2s ease-in-out;
    -webkit-transition: background-image 2s ease-in-out;
    transition: background-image 2s ease-in-out; }

@media (min-width: 768px) {
  .navbar .navbar-nav {
    display: inline-block;
    float: none; }
  .navbar .navbar-collapse {
    text-align: center; }
  #nav {
    list-style: none; } }

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
  background: #fff;
  color: #0a402b; }

.navbar-default .navbar-nav > li > a {
  -o-transition: opacity 0.7s ease-in-out, color 0.7s;
  -webkit-transition: opacity 0.7s ease-in-out, color 0.7s;
  transition: opacity 0.7s ease-in-out, color 0.7s; }

.navbar-default .navbar-nav:hover > li > a {
  filter: alpha(opacity=45);
  opacity: 0.45;
  -o-transition: opacity 0.7s ease-in-out, color 0.7s;
  -webkit-transition: opacity 0.7s ease-in-out, color 0.7s;
  transition: opacity 0.7s ease-in-out, color 0.7s; }

.navbar-default .navbar-nav > li > a:hover {
  /*Opacity*/
  zoom: 1;
  filter: alpha(opacity=100);
  opacity: 1;
  color: #0a402b;
  -o-transition: opacity 0.7s ease-in-out, color 0.7s;
  -webkit-transition: opacity 0.7s ease-in-out, color 0.7s;
  transition: opacity 0.7s ease-in-out, color 0.7s; }

.container-fluid {
  padding: 0; }

@media (min-width: 34em) {
  .gsp-main {
    padding-left: 10%;
    padding-right: 10%;
    margin-left: 0px;
    margin-right: 0px; }
  .space {
    display: block;
    height: 20px; }
  .space-sm {
    display: block;
    height: 10px; }
  .space-lg {
    display: block;
    height: 30px; } }

@media (min-width: 48em) {
  .gsp-main {
    padding-left: 15%;
    padding-right: 15%;
    margin-left: 62px;
    margin-right: 62px; }
  .space {
    display: block;
    height: 30px; }
  .space-sm {
    display: block;
    height: 20px; }
  .space-lg {
    display: block;
    height: 40px; } }

@media (min-width: 62em) {
  .gsp-main {
    padding-left: 12%;
    padding-right: 12%;
    margin-left: 62px;
    margin-right: 62px; }
  .space {
    display: block;
    height: 30px; }
  .space-sm {
    display: block;
    height: 20px; }
  .space-lg {
    display: block;
    height: 30px; } }

@media (min-width: 75em) {
  .gsp-main {
    padding-left: 15%;
    padding-right: 15%;
    margin-left: 62px;
    margin-right: 62px; }
  .space {
    display: block;
    height: 30px; }
  .space-sm {
    display: block;
    height: 20px; }
  .space-lg {
    display: block;
    height: 30px; } }

.gsp-main {
  text-align: justify;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #ebebe8;
  border-color: #fff;
  font-family: "Slabo 27px", serif;
  color: #0a402b; }

footer {
  font-family: "Slabo 27px", serif;
  text-align: center;
  padding: 20px 0 20px 0; }



