.publication {
  display: -ms-flexbox;
  display: flex; }

dl {
  width: 100%;
  overflow: hidden; }

dt {
  float: left;
  width: 24%; }

dd {
  float: left;
  width: 70%;
  padding-top: 0px;
  padding-left: 10px; }

.publication .figure img {
  max-width: 100%;
  -webkit-box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5); }

.publication .figure {
  padding: 6px;
  overflow: visible; }

.publication .ref .ptitle {
  font-weight: bold; }

.publication .ref .authors:after {
  content: "\a";
  white-space: pre; }

.publication .ref .ptitle:after {
  content: "\a";
  white-space: pre; }

.publication .ref .ptype {
  font-style: italic;
  font-weight: bold; }

.publication .ref .description:after {
  content: "\a";
  white-space: pre; }

.publication .ref .booktitle {
  font-style: italic; }

.publication .ref .year:after {
  content: "\a";
  white-space: pre; }



